import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Model } from 'src/app/api/models/model';
import { RequestService } from 'src/app/api/request.service';
import { UtilPhotoService } from 'src/app/services/util-photo.service';
import { DialogService } from 'src/app/api/util/dialog.service';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.page.html',
  styleUrls: ['./profile.page.scss'],
})
export class ProfilePage implements OnInit {

  public id : any ;

  public user : any;
  private userModel : Model;
  private firstLoading = true;


  constructor(
    public route : ActivatedRoute,
    private request : RequestService,
    public utilPhoto : UtilPhotoService,
    private dialogService : DialogService
  ) {
    this.id = this.route.snapshot.paramMap.get('id');

    this.userModel = new Model('User' , request );
   }

  ngOnInit() {
    this.init();
  }


  async init($event = null){
    
    if(this.firstLoading){
      await this.dialogService.loadingProgres();
      this.firstLoading = false;
    }
    
    this.userModel.api_functionModel(this.id , 'profilePage').subscribe(
      response => {
        if(response['status'] == 'success'){
          this.user = response['data'];
        }

        if($event)
          $event.target.complete();
        console.log(response);
        this.dialogService.dimissLoaging();
      },
      error => {
        if($event)
          $event.target.complete();
        console.log(error)
        this.dialogService.dimissLoaging();
      }
    )

  }

}
